body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.fancy-font{font-family:Playball}.bottom-out{margin-bottom:0}.top-1x{margin-top:1rem}.top-2x{margin-top:2rem}.bottom-1x{margin-bottom:1rem}.bottom-2x{margin-bottom:2rem}.larger-1x{font-size:1.25rem}.larger-2x{font-size:1.75rem}.larger-3x{font-size:3rem}@media only screen and (max-width: 40em){.larger-3x{font-size:2rem}}.right-1x{margin-right:1rem}@media only screen and (max-width: 40em){.hide-small{display:none}}.fa-inverse{color:#cd5c5c}.teal{color:#00aeae}@media only screen and (max-width: 40em){.negate{padding-right:0;padding-left:0}}.display-4{font-size:6.5rem;color:#ffffe0 !important;text-shadow:2px 2px #000}@media only screen and (max-width: 60em){.display-4{font-size:3.5rem}}@media only screen and (max-width: 40em){.display-4{font-size:3.5rem}}.app.wrapper{z-index:99;background-color:#fff}.app h1{margin:2rem auto}.app h2{font-size:2.5rem;margin:auto auto 2rem auto}@media only screen and (max-width: 40em){.app h2{font-size:2rem}}.jumbotron{padding:2.5rem 1rem 5rem 1rem;background-image:url("https://raw.githubusercontent.com/CrystalCodes01/brystalflwed/master/brystalwed/public/romantic.jpg");background-size:cover;background-position:100% 85%;height:400px;margin:1rem}@media only screen and (max-width: 60em){.jumbotron{height:325px}}@media only screen and (max-width: 40em){.jumbotron{height:450px}}.jumbotron .lead{font-size:3rem;color:#ffffe0 !important}nav .navbar-brand{font-family:fantasy;color:#282c34 !important;margin:0.25rem 1rem auto 1rem}@media only screen and (max-width: 40em){nav .navbar-brand{margin:0.5rem 1rem auto 1rem}}nav .nav-item{margin:auto 1rem}nav .nav-item:hover,nav .nav-item:active,nav .nav-item:focus{font-weight:bold}nav .nav-item a{color:#282c34 !important}.icon-header{font-size:2rem}footer{z-index:-99}.table-itinerary thead th.medium-expand{width:200px}@media only screen and (max-width: 60em){.table-itinerary thead th.medium-expand{width:150px}}@media only screen and (max-width: 40em){.table-itinerary thead th.medium-expand{width:110px}}.timer-wrapper{text-align:center;font-size:2rem;padding:1rem;margin-top:2rem}.timer-wrapper .clock{display:inline;margin:10px}@media only screen and (max-width: 60em){.timer-wrapper .clock{display:block}}@media only screen and (max-width: 40em){.timer-wrapper .clock{display:block}}#countDown{border:4px solid #00aeae;border-radius:4px;-webkit-animation:mymove 7s infinite;animation:mymove 7s infinite}@-webkit-keyframes mymove{50%{border-color:#32a7d6}}@keyframes mymove{50%{border-color:#f8f8ff}75%{border-color:coral}}.subtext-with-animation{vertical-align:middle;letter-spacing:4px;text-shadow:2px 2px #000}@media only screen and (max-width: 60em){.subtext-with-animation{left:35%;top:8.5rem}}@media only screen and (max-width: 40em){.subtext-with-animation{left:4rem;top:14.5rem}}.subtext-with-animation span:first-of-type{opacity:0;-webkit-animation:word-animation 0.8s ease-out 1.7s forwards;animation:word-animation 0.8s ease-out 1.7s forwards}.subtext-with-animation span:nth-of-type(2){opacity:0;-webkit-animation:word-animation 1s ease-out 2.6s forwards;animation:word-animation 1s ease-out 2.6s forwards}@-webkit-keyframes word-animation{0%{top:10vw;opacity:0}100%{top:3.5vw;opacity:1}}@keyframes word-animation{0%{top:10vw;opacity:0}100%{top:3.5vw;opacity:1}}

